const tools = {

  /**
   * @description 
   * @param {Object} obj
   */ 
  isObject(obj) {
    if(typeof obj === 'object' && obj != null) {
      if(obj instanceof Array) {
        return false
      }
      return true
    }
    return false
  },

  /**
   * @description 
   * @param {Array} arr
   */ 
  isArray(arr) {
    return arr instanceof Array
  },

  /**
   * @description /
   * @param {Any} val1
   * @param {Any} val2
   */ 
  isEqual(val1, val2) {
    const isObject1 = (typeof val1 === 'object' && val1 != null)
    const isObject2 = (typeof val2 === 'object' && val2 != null)

    if(!isObject1 || !isObject2) {
      return val1 === val2
    }

    if(val1 === val2) {
        return true
    }

    const val1Keys = Object.keys(val1)
    const val2Keys = Object.keys(val2)
    if(val1Keys.length !== val2Keys.length) {
        return false
    }

    for(let key in val1) {
        const result = this.isEqual(val1[key], val2[key])
        if(!result) {
          return false
        }
    }
    
    return true
  },

  /**
   * @description  true
   * @param {Any} value
   */ 
  isLousy(value) {
    return !this.isYummy(value)
  },

  // 
  isEmpty(value) {
    return !this.isYummy(value)
  },

  /**
   * @description  true
   * @param {Any} value
   */ 
  isYummy(value) {
    if(value === undefined 
      || value == null 
      || value === '' 
      || value === false
    ) {
      return false
    }
    
    else if(value instanceof Array) {
      if(value.length === 0) {
        return false
      }
    }
    
    else if(typeof value === 'object') {
      var keys = Object.keys(value)
      if(keys.length === 0) {
        return false
      }
    }
    
    return true
  },

  /**
   * @description  undefined
   * @param {Object[]} value
   * @param {Array|Object|String} target
   */ 
  find(value, target) {
    if(this.isArray(target)) {
      const [key, val] = target
      for(let item of value) {
        if(item[key] === val) {
          return item
        }
      }
    }
    else if(this.isObject(target)) {
      for(let item of value) {

        const variety = Object.entries(target)

        const result = variety.every((v) => {
          const [key, val] = v
          return item[key] === val
        }) 

        if(result) {
          return item
        }

      }
    }else {
      for(let item of value) {
        for(let key in item) {
          if(item[key] === target) {
            return item
          }
        }
      }
    }
  }
} 

export default tools